import React, { useState } from 'react'
import { Nav, Navbar, Button, Container, Modal, Form } from 'react-bootstrap';
import { Link } from "react-router-dom";
import styled from "styled-components";
import logo from "./img/1200px-React-icon.svg.png"

const Styles = styled.div`
  a, .navbar-brand, .mavbar-nav .nav-link{
      color: #adb1b8;
      text-decoration:none;
      a:hover{
      color: rgb(0, 197, 246);
      border-bottom: 3px solid rgb(0, 197, 246);
      }
  }
`;

export default function NaviBar() {


    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
            <Styles>

                <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                    <Container>
                        <Navbar.Brand href="#home"> <img width="50px" src={logo} alt="logo" /> </Navbar.Brand>
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                        <Navbar.Collapse id="responsive-navbar-nav">
                            <Nav className="mr-auto" >
                                <Nav.Link > <Link to="/test-react-site1"> Home</Link> </Nav.Link>
                                <Nav.Link><Link to="/test-react-site1/about"> About</Link> </Nav.Link>
                                <Nav.Link><Link to="/test-react-site1/users"> Users</Link> </Nav.Link>
                            </Nav>
                            <Nav>
                                <Button variant="primary" className="mr-2" onClick={handleShow}>Log in</Button>

                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>

            </Styles>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Log In</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Email address</Form.Label>
                            <Form.Control type="email" placeholder="Enter email" />
                            <Form.Text className="text-muted">
                                We'll never share your email with anyone else.
                         </Form.Text>
                        </Form.Group>

                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" placeholder="Password" />
                        </Form.Group>
                        <Form.Group controlId="formBasicCheckbox">
                            <Form.Check type="checkbox" label="Check me out" />
                        </Form.Group>
                        <Button variant="primary" type="submit">
                            Submit
                         </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        </>
    )
}

