import React from "react";
import { Container } from "react-bootstrap";
import logo from "./img/1200px-React-icon.svg.png"
import { Navbar } from "react-bootstrap"

const Footer = () => {
    return (

        <Navbar bg="dark" variant="dark" className="navbar-fixed-bottom row-fluid">
            <Navbar.Brand href="#home">
                <img
                    alt="logo"
                    src={logo}

                    height="30"
                    className="d-inline-block align-bottom"
                />{' '}
            </Navbar.Brand>
        </Navbar>



    )
}
export default Footer;