import React from "react";
import { Jumbotron as Jumbo, Container } from "react-bootstrap";
import jumb from "../Components/img/pexels-photo-3183153.jpeg";
import styled from "styled-components";


const Styles = styled.div`
.jumbo{
    background : url(${jumb}) no-repeat fixed bottom;
    background-size: cover;
    color :#efefef;
    height:400px;
    position:relative;
    z-index:-2;
}
.overlay {
    background-color:#000;
    opacity:0.7;
    position :absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    z-index:-1;
}

`
const Jumbotron = () => {
    return (
        <Styles>
            <Jumbo fluid className="jumbo">
                <div className="overlay"></div>
                <Container>
                    <h1>Web Blog</h1>
                    <p> Lorem ipsum dolor sit amet, consectetur adipisicing elit. Similique, facilis. Harum explicabo dolorum
                    minima molestias adipisci fugiat vitae fuga similique reprehenderit, sunt ad quisquam nihil, debitis
                    facere, optio non accusamus?Lorem ipsum dolor sit amet, consectetur adipisicing elit. Similique, facilis. Harum explicabo dolorum
                    minima molestias adipisci fugiat vitae fuga similique reprehenderit, sunt ad quisquam nihil, debitis
            facere, optio non accusamus?</p>
                </Container>
            </Jumbo>
        </Styles>
    )
}

export default Jumbotron;